body {
  margin: 0;
  min-width: 450px;
  overflow: overlay;
}
html,
body,
#root {
  height: 100%;
  background: #131418;

  font-family: "Montserrat", Arial, sans-serif;
  font-weight: normal;
  font-style: normal;
}
::-webkit-scrollbar {
  width: 1px;
  background: #00000000;
}

input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
